  transition: background 0.2s 0;
  background: transparent;
}
.toggler-burger--open .toggler-burger__icon:before,
.toggler-burger--open .toggler-burger__icon:after {
  transition: top 0.2s, transform 0.2s 0.2s;
  top: 0;
