.toggler-burger__icon:after {
  width: 100%;
  height: 0.3rem;
  background: #1c1c1c;
  border-radius: 0.2rem;
  position: absolute;
  left: 0;
  content: '';
  transform-origin: 50% 50%;
  transition: top 0.2s 0.2s, transform 0.2s;
}
.toggler-burger__icon:before {
  top: 1rem;
}
.toggler-burger__icon:after {
  top: -1rem;
